@tailwind base;
@tailwind components;
@font-face {
    font-family: "peyda";
    src: url("./font/woff2/PeydaWeb-Regular.woff2") format("woff2"),
    url("./font/woff/PeydaWeb-Regular.woff") format("woff");
    font-weight: normal;
}
@font-face {
    font-family: "peyda";
    src: url("./font/woff2/PeydaWeb-Medium.woff2") format("woff2"),
    url("./font/woff/PeydaWeb-Medium.woff") format("woff");
    font-weight: medium;
}
@font-face {
    font-family: "peyda";
    src: url("./font/woff2/PeydaWeb-Bold.woff2") format("woff2"),
    url("./font/woff/PeydaWeb-Bold.woff") format("woff");
    font-weight: bold;
}
@tailwind utilities;


body{
    font-family: 'peyda', 'sans-serif', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana;
}

.active {
    color: #0E9594;
}

.active > div > div > svg > path {
    fill: #0E9594;
}

.navbarItem:hover > label > .chevronLeft{
    transform: rotate(-90deg);
}

.navLogo > a > svg > path {
    transition: all .3s ease;
}

.navLogo:hover > a > svg > path {
    fill: #0E9594;
}

.navbarItemBox {
    position: absolute;
    top: 100%;
    right: 0;
    width: 100%;
    transform-origin: top;
    transform: scale(1, 0);
}

.navbarItem:hover > .navbarItemBox{
    transform: scale(1, 1);
}

.megaIcon > svg > path {
    transition: all .3s ease;
}

.megaContent:hover > .megaIcon > svg > path, .megaText > h3 {
    fill: #0E9594;
}

.megaContent:hover > .megaText > h3 {
    color: #0E9594;
}

.megaContent:hover > .megaText > p {
    color: #A3BFBF;
}

.megaContentHover {
    transition: all .3s ease;
    opacity: 0;
}

.megaIcon:hover > .megaContentHover{
    opacity: 0.9;
}

.megaContent:hover img {
    transform: scale(1.2);
}

.faqItem {
    margin-top: 2rem;
}
.faqItem:hover {
    margin-top: 1rem;
}














/* Animations */

@keyframes animRect {
    0% {}
    50% {
        left: 15%;
        bottom: 70%;
        transform: rotate(-45deg);
    }
    100% {
        left: 10%; 
        bottom: 10%;
        transform: rotate(-90deg);
    }
}

.animRect{
    position: absolute;
    left: 10%; 
    bottom: 10%;
    animation: animRect 30s linear 0s infinite forwards;
}

@keyframes animCircle {
    0% {}
    25% {
        transform: scale(1.5);
    }
    50% {
        right: 15%;
        bottom: 60%;
        transform: rotate(-45deg);
    }
    75% {
        transform: scale(1.5);
    }
    100% {
        right: 30%; 
        bottom: 20%;
        transform: rotate(-90deg);
    }
}

.animCircle{
    position: absolute;
    right: 30%; 
    bottom: 20%;
    animation: animCircle 25s linear 0s infinite forwards;
}

